ul,
ol,
li {
  list-style: none;
}

//h1, h2, h3, h4, h5, h6 {
//  font-size: inherit;
//  font-weight: inherit;
//}

*{padding: 0; margin: 0; border: 0;}
*, *:before, *:after{-moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus, a:active{outline: none;}
a, a:visited{text-decoration: none;}
a:hover{text-decoration: none;}
ul li{list-style: none;}
img{vertical-align: top;}

input,button,textarea{font-family: inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer;}
button::-moz-focus-inner {padding: 0;border: 0;}

//=====COLORS
$color-background_layout: #8b91a1;
$color-background_main: #6e7ca1;
$color-text: #242528;
$color-title-text: #259725;
$color-border: #f67e70;
$color-button: #376e6f;
$color-button_hover: #1c3334;
$color-button_text: #f50606;
$color-button_background: rgba(0, 0, 0, 0.7);
$color-block-background: rgba(0, 0, 0, 0.3);


html {
  height: 100%;
  font-size: 10px;
  overflow-x: hidden;
  margin-right: calc(-1 * (100vw - 100%));
}

body {
  background-color: #fff7f2;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  height: 100%;  
  display: flex;
  flex-direction: column;
  margin: 0;
  font-size: 1.6rem;
  color: $color-text;
}

.wrapper{
  display: flex;
  flex-direction: column;
  height: 100%;
}
